  font-size: 0.95rem;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  border: 1px solid var(--gold);
  border-radius: 2px;
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
